import { Component, OnInit } from '@angular/core';
import { takeUntil, switchMap, map } from 'rxjs/operators';
import { BaseComponent } from '../../common/BaseComponent';
import { DocumentService } from '../document.service';
import { Subject } from 'rxjs';

interface Pagedocument {
	documents: any[];
	page: number;
}

@Component({
	selector: 'app-document-list',
	templateUrl: './document-list.component.html',
	styleUrls: ['./document-list.component.scss'],
})
export class DocumentListComponent extends BaseComponent implements OnInit {
	currentPage: number = 0;
	documents: any[];
	documentsPage: Subject<number> = new Subject();

	constructor(private documentSvc: DocumentService) {
		super();
	}
	ngOnInit(): void {
		this.documentsPage.pipe(
			switchMap((page) => {
				return this.documentSvc.getDocuments(page).pipe(
					map((documents) => ({ documents, page })),
				);

			}),
			takeUntil(this.unsubscribe),
		).subscribe(({ documents, page }) => {
			this.currentPage = page;
			this.documents = documents;
		});

		this.documentsPage.next(0);
	}

	nextPage() {
		this.documentsPage.next(++this.currentPage);
	}
}
